import React from "react";
import './main_page.css';

function About() {
    
    return (
        <div>
            <div className="col">
                   <div style={headText}>วัน-เวลาขาย</div> 
                   <div style={detailText}>จันทร์ - ตลาดนัดหลักสี่ 15.30 - 21.00</div> 
                   <div style={detailText}>อังคาร,ศุกร์และอาทิตย์ - ตลาดนัดมหานคร 15.30 - 21.00 </div>
                   <div style={detailText}>พุธและพฤหัส - ตลาดนัดแฟลต์บางบัว 15.30 - 20.30</div>
                   <div style={detailText}>เสาร์ - หยุด</div>
            </div>
        </div>

    );
}

  const headText = {
    fontFamily: "sans-serif",
    fontSize: 26,
    color: 'green',
    fontWeight: "bold"
  }

  const detailText = {
    fontFamily: "sans-serif",
    fontSize: 16,
  }
export default About;